/* htAutocompleteArrow */
.handsontable {
    .htAutocompleteArrow { 
        width: 16px; 
        height: 16px; 
        font-size: 0;
        position: relative;
        top: 50%;
        transform: translateY(-50%);

        &:after {
            width: 4px;
            height: 4px;
            content:'';
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: rotate(45deg) translate(-100%);
            border-right: 1px solid var(--hot-read-only-text);
            border-bottom: 1px solid var(--hot-read-only-text)
        }
    }
}