/*
    Handsontable Column Move
*/
@import "../../../ht/mixins";

.handsontable {
    .wtHider {
        position: relative;
    }

	.ht__manualColumnMove {
		&.after-selection--columns thead th.ht__highlight {
			@include coursor-grab; 
		}
		&.on-moving--columns {
			& *,
			thead th.ht__highlight {
				@include coursor-grabbing;
			}

			.manualColumnResizer {
				display: none;
			}
		}
		
		&--guideline,
		&--backlight {
			position: absolute;
			height: 100%;
			display: none;
		}

		&--guideline {
			background: var(--hot-manualMove-guideline-color);
			width: var(--hot-manualMove-guideline-size);
			top: 0;
			margin-inline-start: calc(var(--hot-manualMove-guideline-size) * -.5);
			margin-inline-end: 0;
			z-index: 205;
		}

		&--backlight {
			background: var(--hot-manualMove-backlight-color);
			display: none;
			z-index: 205;
			pointer-events: none;
		}
	}

	&.on-moving--columns {
		&.show-ui .ht__manualColumnMove--guideline,
		.ht__manualColumnMove--backlight {
			display: block;
		}
	}
}